import { Link, BrowserRouter as Router } from "react-router-dom";
import logo from "../img/logo-navbar.png";
import "../style/nav.css";

function Nav() {
  function btnClick() {
    document.getElementById("button").classList.toggle("toogle");
    document.getElementById("links").classList.toggle("nav-active");
    document.querySelectorAll("#link").forEach((link, index) => {
      if (link.style.animation) {
        link.style.animation = "";
      } else {
        link.style.animation = `navLinkFade 0.5s ease forwards ${0.3 + index / 7}s`;
      }
    });
  }
  return (
    <nav className="navbar">
      <Router>
        <Link to="/">
          <img className="navlogo" src={logo} alt="Uizarq" />
        </Link>
        <ul className="links" id="links">
          <li className="link" id="link">
            <Link to="">Proyectos</Link>
          </li>
          <li className="link" id="link">
            <Link to="">Contactenos</Link>
          </li>
        </ul>
      </Router>
      <div className="icon" id="button" onClick={btnClick}>
        <div className="btn1" />
        <div className="btn2" />
        <div className="btn3" />
      </div>
    </nav>
  );
}
export default Nav;
